<template>
  <v-main
    class="base-layout full-width d-flex flex-column"
    :class="{ 'white-background': whiteBackground, 'grey-background': greyBackground }"
  >
    <div
      v-if="isHeaderShowed"
      class="header d-flex justify-space-between align-center py-xs-4 white px-3 px-sm-7"
      :class="[{
        'none-shadow': noShadow,
        'sticky-desktop-1': !$vuetify.breakpoint.mobile && $route.name === $constants.ROUTE_NAMES.LOADS,
      }, headerClasses]"
    >
      <slot name="header"></slot>
      <main-navigation-bottom
        v-if="!$vuetify.breakpoint.mobile"
        :show-dispatcher-profile-link="showDispatcherProfileLink"
      />
    </div>

    <div
      v-if="loading"
      class="relative d-flex flex-grow-1"
    >
      <template>
        <v-container
          class="white"
          fluid
          d-flex
          flex-grow-1
          justify-center
          align-center
        >
          <v-overlay
            opacity="0.11"
            absolute
          >
            <v-progress-circular
              class="full-width"
              :size="60"
              :width="6"
              style="width: 100%;"
              color="primary-dark"
              indeterminate
            />
          </v-overlay>
        </v-container>
      </template>
    </div>
    <slot
      v-else
      name="content"
    ></slot>
  </v-main>
</template>

<script>
import MainNavigationBottom from '@components/Navigation/MainNavigationBottom';
import { mapState } from 'vuex';
import ROLES from '@utils/constants/roles';

export default {
  name: 'BaseLayout',
  components: {
    MainNavigationBottom,
  },
  props: {
    isHeaderShowed: {
      type: Boolean,
      default: true,
    },
    noShadow: {
      type: Boolean,
      default: false,
    },
    headerClasses: {
      type: Array,
      default: () => [],
    },
    whiteBackground: {
      type: Boolean,
      default: false,
    },
    greyBackground: {
      type: Boolean,
      default: true,
    },
    loading: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    showDispatcherProfileLink() {
      return this.userRole === ROLES.DISPATCHER;
    },
    ...mapState(['userRole']),
  },
};
</script>

<style lang="scss" scoped>
  .sticky-desktop-1 {
    position: sticky;
    top: 0;
    z-index: 1;
  }
  ::v-deep .v-overlay__content {
    width: 60px;
  }

  .base-layout {
    &.grey-background {
      background-color: $page-background;
    }

    &.white-background {
      background-color: $white;
    }
  }

  .header {
    box-shadow: $app-elevation-secondary;
    min-height: 70px;

    &.none-shadow {
      box-shadow: none;
    }

    @media screen and (max-width: 1264px) {
      box-shadow: none;
      min-height: 84px;
    }
  }
</style>
