<template>
  <div>
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <div
          v-ripple
          class="d-flex flex-row-reverse rounded-circle ml-3"
          v-on="on"
          v-bind="attrs"
          @click="toggleMenu"
        >
          <div class="d-flex justify-center">
            <div
              ref="accoutIcon"
              class="account-icon-wrapper d-flex justify-center"
            >
              <img
                class="account-icon"
                :src="require('@images/icons/main-navigation/account.svg')"
              />
            </div>
          </div>
        </div>
      </template>
      <span>Click to {{ menuOpened ? 'close' : 'open' }} menu</span>
    </v-tooltip>

    <v-menu
      v-model="menuOpened"
      :position-x="x"
      :position-y="y"
      offset-y
      transition="scroll-x-reverse-transition"
    >
      <v-card :max-width="224">
        <v-list dense>
          <template v-if="showDispatcherProfileLink && name">
            <v-list-item>
              <v-list-item-content>
                <v-list-item-subtitle class="body-1 pb-1 font-weight-medium primary-dark--text text-truncate">
                  {{ name }}
                </v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>

            <v-divider class="mx-2" />
          </template>

          <ul class="mt-2">
            <li>
              <router-link
                v-ripple
                :to="{ name: $constants.ROUTE_NAMES.SETTINGS }"
                active-class="active"
                class="d-flex align-center body-2 font-weight-medium profile-nav--text text-decoration-none px-4 py-2"
              >
                <v-img
                  class="mr-3"
                  :src="require('@images/icons/main-navigation/gear.svg')"
                  max-width="16"
                />

                <p class="ma-0">
                  Settings
                </p>
              </router-link>
            </li>

            <li>
              <router-link
                v-ripple
                :to="{ name: $constants.ROUTE_NAMES.PROFILE }"
                active-class="active"
                class="d-flex align-center body-2 font-weight-medium profile-nav--text text-decoration-none px-4 py-2"
              >
                <v-img
                  class="mr-3"
                  :src="require('@images/icons/main-navigation/carrier.svg')"
                  max-width="16"
                />

                <p class="ma-0">
                  Carrier Profile
                </p>
              </router-link>
            </li>

            <li v-if="showDispatcherProfileLink">
              <router-link
                v-ripple
                :to="{ name: $constants.ROUTE_NAMES.DISPATCHER_PROFILE }"
                active-class="active"
                class="d-flex align-center body-2 font-weight-medium profile-nav--text text-decoration-none px-4 py-2"
              >
                <v-img
                  class="mr-3"
                  :src="require('@images/icons/main-navigation/dispatcher.svg')"
                  max-width="16"
                />

                <p class="ma-0">
                  Dispatcher Profile
                </p>
              </router-link>
            </li>

            <li>
              <div
                v-ripple
                class="d-flex cursor-pointer align-center body-2 font-weight-medium profile-nav--text text-decoration-none px-4 py-2"
                @click="logOut"
              >
                <v-img
                  class="mr-3"
                  :src="require('@images/icons/main-navigation/logout.svg')"
                  max-width="15"
                />

                <p class="ma-0">
                  Log Out
                </p>
              </div>
            </li>
          </ul>
        </v-list>
      </v-card>
    </v-menu>
  </div>
</template>

<script>
import { createNamespacedHelpers, mapState } from 'vuex';
import { actions } from '@modules/auth/constants';
import * as constants from '@modules/dispatcher/constants';

const { mapActions } = createNamespacedHelpers('auth');
const { mapActions: dispatcherActions } = createNamespacedHelpers('dispatcher');

export default {
  name: 'MainNavigationBottom',
  props: {
    showDispatcherProfileLink: {
      type: Boolean,
      required: true,
    },
  },
  data: () => ({
    menuOpened: false,
    x: 0,
    y: 0,
    name: '',
  }),
  computed: {
    ...mapState(['userId']),
  },
  mounted() {
    if (this.showDispatcherProfileLink) {
      this.getDispatcherProfile({ id: this.userId })
        .then((data) => {
          this.name = data.name;
        }).catch((errors) => {
          if (Array.isArray(errors)) {
            this.serverError = errors;
          } else {
            this.serverErrors = errors;
          }
        });
    }
  },
  methods: {
    ...dispatcherActions({
      getDispatcherProfile: constants.actions.GET_DISPATCHER_PROFILE,
    }),
    toggleMenu() {
      const { left, bottom } = this.$refs.accoutIcon.getBoundingClientRect();
      this.x = left - 190;
      this.y = bottom + 11;
      this.menuOpened = !this.menuOpened;
    },
    ...mapActions({
      signout: actions.SIGN_OUT,
    }),
    logOut() {
      this.signout({ vm: this });
    },
  },
};
</script>

<style scoped>
.account-icon-wrapper {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  display: flex;
  z-index: 1;
}

.account-icon {
  max-width: 35px;
  max-height: 100%;
  width: auto;
  height: auto;
}

.active {
  background-color: var(--v-active-link-background-base);
}
</style>
